<template>
    <div class="carousel" id="carousel">
    <keyDo
      ref="keydo"
      v-on:listenKeyCode="keyCode"
      v-if="isShow">
    </keyDo>
  </div>
</template>
<style scoped="UserNotice">
  .carousel{
    width: 1280px;
    height: 720px;
  }
</style>
<script>
import {
  mapGetters
} from 'vuex';
import {HTTPUtil as api} from '../../fetch/api.js'
export default {
  props: ['jsonUrl', 'bp'],
  name: 'UserNotice',
  data(){
    return{
      isShow: false
    }
  },
  created () {
    this.isShow = true;
    this.getDetail(this.jsonUrl);
  },
  methods: {
    getDetail (curl) {
      var url = curl + this.behindParams;
      api.jsonp(url, (res) => {
        var img = res.data.specialImg;

        document.getElementById('carousel').style.backgroundImage = 'url(' + img + ')';
      });
    },
    keyCode (params) {
      // console.log("父组件接收子组件触发事件传上来的值");
      if (params == 'KeyBack') {
        this.KeyBack();
      }
    },
    KeyBack () {
      if (this.$route.query.jsonUrl != undefined) {
        console.log('详情页从外部打开');
        yh.back();
      } else {
        let bst = localStorage.getItem('bp');
        if (bst == '收藏跳入') {
          localStorage.removeItem('bp');
          // this.$router.go(-1);
          this.$router.push({path: '/History'});
        } else {
          this.$router.push({path: '/jx/index'});
        }
      }
    }
  },
  computed: {
    ...mapGetters(['behindParams', 'navpos'])
  }
};
</script>



